import uniRate from '@/components/uni-rate/uni-rate.vue';
export default {
	components:{
		uniRate,
	},
	data() {
		return {
			topLeftText: "粤玲珑广式早茶",
			rate: 0, // 评分
			comCont: "", // 评论
			imgList: [], // 预览本地地址
			allImgTNum: 0, //所有图片数量
			showImage: false,// 展示图片标识
			filePath:[], // 线上图片地址
		};
	},
	onLoad(options) {
		if (options.src) {
			this.showImage = true;
			this.imgList.push(options.src);
			this.filePath.push(options.tem);
		}
		uni.setNavigationBarTitle({
			title: this.topLeftText
		})
	},
	methods:{
		chooseImage() {
			this.$http.uploadImage(1, (res, tem)=> {
				this.showImage = true;
				this.imgList.push(...tem);
				this.filePath.push(res.data.url);
			})
		},
		//预览图片
		viewImage(e) {
			uni.previewImage({
				urls: this.imgList,
				current: e.currentTarget.dataset.url
			});
		},
		// 删除图片
		delImage(e) {
			uni.showModal({
				title: '删除照片',
				content: '确定要删除这张照片吗？',
				cancelText: '取消',
				confirmText: '确定',
				success: res => {
					if (res.confirm) {
					this.imgList.splice(e.currentTarget.dataset.index, 1);
					this.filePath.splice(e.currentTarget.dataset.index, 1);	
					}
					if (this.imgList.length === 0) {
						this.showImage = false;
					}
				}
			})
		},
		confirmBtn() {
			if(this.rate == 0) {
				uni.showToast({
					icon: "none",
					title: "店铺评分不能为空~",
					duration: 2000
				})
				return;
			}
			if(this.comCont == "") {
				uni.showToast({
					icon: "none",
					title:"评论留言不能为空哦~",
					duration: 2000
				})
				return;
			}
			if (this.imgList.length == 0) {
				uni.showToast({
					icon: "none",
					title:"至少需要一张图片哦~",
					duration: 2000
				})
				return;
			}
			
			const uid = this.$db.get("userinfo").user_id;
			
			const params = {
				uid,
				sid: 1,
				content: this.comCont,
				score: Number(this.rate)*2,
				files: this.filePath.join(',')
			}
		
			this.$http.postPushComment(params,res=>{
				if(res.code == 200) {
					
					uni.navigateTo({
						url: "/pages/index/success"
					})
				} else {
					this.$common.errorToShow(res.msg);
				}
			})
			
		},
		cancelBtn() {
			uni.navigateBack({
				delta: 1
			})
		},
		changeRate(e) {
			this.rate = e.value;
		},
		navBack() {
			uni.navigateBack({
				delta: 1
			})
		}
	}
}
